/* Container for the whole cart section */
.cartDisplay {
  position: relative;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
  background-color: #f9f9f9;
  min-height: 100vh;
  box-sizing: border-box;
}

/* Header styles: flex container with space between title and cart icon */
.cartDisplay-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-bottom: 2px solid #ddd;
  background-color: #fff;
  position: sticky;
  top: 0;
  z-index: 100;
  user-select: none;
}

.cartDisplay-header h2 {
  margin: 0;
  font-size: 1.8rem;
  color: #333;
  font-weight: 600;
}

/* Cart icon container styling */
.cartDisplay-header > div {
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 8px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.cartDisplay-header > div:hover {
  background-color: #e0e0e0;
}

/* ---------- CartSider Styles ---------- */
/* Sidebar container: fixed position, right side, full height */
.cartSider {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 320px;
  background-color: #fff;
  box-shadow: -3px 0 10px rgba(0, 0, 0, 0.1);
  padding: 24px;
  box-sizing: border-box;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 200;
  display: flex;
  flex-direction: column;
}

/* Sidebar open state */
.cartSider.open {
  transform: translateX(0);
}

/* Sidebar header (for title and close button) */
.cartSider-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.cartSider-header h3 {
  margin: 0;
  font-size: 1.4rem;
  font-weight: 600;
  color: #222;
}

/* Close button styling */
.cartSider-closeBtn {
  background: none;
  border: none;
  font-size: 1.8rem;
  cursor: pointer;
  color: #999;
  transition: color 0.2s ease;
}

.cartSider-closeBtn:hover {
  color: #333;
}

/* Cart content area */
.cartSider-content {
  flex-grow: 1;
  overflow-y: auto;
  padding-right: 8px;
  color: #444;
}

/* Optional: styling for empty cart message */
.cartSider-empty {
  text-align: center;
  font-size: 1rem;
  color: #666;
  margin-top: 40px;
}

/* ---------- CartIcon Styling ---------- */
/* Just in case CartIcon is a button or div */
.cartIcon {
  width: 36px;
  height: 36px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0070f3;
  border-radius: 50%;
  color: white;
  font-weight: 700;
  font-size: 1.2rem;
  user-select: none;
  transition: background-color 0.3s ease;
}

.cartIcon:hover {
  background-color: #005bb5;
}

/* Badge for number of items in cart (if you want) */
.cartIcon-badge {
  position: absolute;
  top: -6px;
  right: -6px;
  background-color: #ff4136;
  color: white;
  font-size: 0.75rem;
  font-weight: 700;
  padding: 2px 6px;
  border-radius: 12px;
  pointer-events: none;
  user-select: none;
}
